@import url(https://fonts.googleapis.com/css2?family=Fuzzy+Bubbles&display=swap);
.Sequencer {
  height: 100vh;
  width: 100vw;
  background: #1c1c1e;
}

.seqContainer {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.instructions {
  height: 60vh;
  width: 60vw;
  background: #1c1c1e;
  margin: 0 auto;
  z-index: 999;
  position: absolute;
  top: 20vh;
  left: 20vw;
  border: 2px solid white;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  background-color: rgba(255, 255, 255, 0.2);
  transition: 0.5s ease-in-out;
  box-shadow: 10px 10px 10px 10px rgba(0.5, 0.5, 0.5, 0.5);
}

.instructions h1 {
  font-family: "Fuzzy Bubbles", cursive;
  color: black;
  font-size: 2rem;
  text-align: center;
  margin: 0 auto;
  padding: 0;
  text-decoration: underline;
}

.instructions ul {
  font-family: "Fuzzy Bubbles", cursive;
  color: black;
  font-size: 1.5rem;
  text-align: center;
  margin: 0 auto;
  padding: 0;
  list-style-type: none;
  line-height: 2em;
}

.loader {
  background: hsl(223, 10%, 10%);
  font: 1em/1.5 sans-serif;
  font-size: calc(16px + (24 - 16) * (100vw - 320px) / (1280 - 320));
  height: 100vh;
  display: grid;
  place-items: center;
}
.bm-pl {
  position: relative;
  width: 12em;
  height: 12em;
}
.bm-pl__blob {
  animation: moveR 8s linear infinite;
  border-radius: 50%;
  mix-blend-mode: screen;
  position: absolute;
  top: 0;
  left: 0;
  width: 9em;
  height: 9em;
}
.bm-pl__blob--r {
  background: hsl(3, 90%, 55%);
}
.bm-pl__blob--g {
  animation-name: moveG;
  background: hsl(113, 90%, 55%);
}
.bm-pl__blob--b {
  animation-name: moveB;
  background: hsl(223, 90%, 55%);
}

/* Animation */
@keyframes moveR {
  from,
  to {
    border-radius: 54% 79% 75% 58% / 70% 66% 71% 47%;
    transform: translate(0, 0) rotate(-35deg) scale(0.875, 1);
  }
  33% {
    border-radius: 79% 43% 56% 67% / 58% 64% 61% 60%;
    transform: translate(2.75em, 0) rotate(0) scale(0.75, 0.875);
  }
  67% {
    border-radius: 55% 77% 56% 67% / 58% 64% 61% 60%;
    transform: translate(1.25em, -0.5em) rotate(0) scale(1.125, 0.875);
  }
}
@keyframes moveG {
  from,
  to {
    border-radius: 60% 59% 51% 58% / 69% 64% 52% 55%;
    transform: translate(2.25em, 1em) rotate(45deg) scale(1.125, 0.875);
  }
  33% {
    border-radius: 51% 95% 72% 59% / 75% 98% 54% 75%;
    transform: translate(0, 0.5em) rotate(0) scale(1, 1);
  }
  67% {
    border-radius: 74% 85% 63% 87% / 81% 100% 62% 82%;
    transform: translate(1em, 2em) rotate(0) scale(0.75, 0.875);
  }
}
@keyframes moveB {
  from,
  to {
    border-radius: 52% 59% 50% 58% / 56% 80% 40% 59%;
    transform: translate(1.75em, 3em) rotate(0) scale(1, 1);
  }
  25% {
    border-radius: 71% 58% 100% 43% / 68% 53% 85% 68%;
    transform: translate(-0.75em, 3em) rotate(10deg) scale(0.75, 1);
  }
  50% {
    border-radius: 99% 62% 87% 69% / 93% 92% 79% 75%;
    transform: translate(2em, 2em) rotate(0) scale(0.7, 0.7);
  }
  75% {
    border-radius: 78% 98% 100% 73% / 75% 76% 100% 91%;
    transform: translate(3.5em, 2em) rotate(0) scale(0.875, 0.875);
  }
}

.overlay {
  display: none;
  position: absolute;
  top: 0;
  background-color: rgba(175, 116, 6, 0.7);
  width: 100%;
  height: 100%;
}
.iconContainer {
  width: 200px;
  height: 200px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -100px;
  margin-top: -100px;
}
.iconContainer p {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 30px;
  font-size: 60px;
  opacity: 0;
  transform: rotate(-90deg);
}

.phone {
  position: relative;
  left: 50%;
  margin-left: -50px;
  width: 0;
  height: 0;
  border: 0 solid #000;
  background: url(/static/media/pexels-stas-knop-3552948.5b96d127.jpg);
  background-position: center;
  background-size: cover;
  border-radius: 10px;
  box-sizing: border-box;
}
.phone i {
  text-align: center;
  width: 100%;
  line-height: 120px;
  font-size: 50px;
  color: #454545;
  opacity: 0;
}
@media only screen and (max-width: 1020px) {
  .overlay {
    display: block;
  }
  .overlay .phone {
    animation: sizeIncrease 0.5s forwards ease,
      borderIncrease 0.5s 0.5s forwards ease, rotateRight 0.7s 1s forwards ease;
  }
  .overlay .phone i {
    animation: fadeIn 0.5s 0.8s forwards ease;
  }
  .overlay .iconContainer p {
    animation: fadeIn 0.5s 0.5s forwards ease;
  }
}

@keyframes sizeIncrease {
  0% {
    width: 0;
    height: 10px;
  }
  50% {
    width: 100px;
    height: 10px;
  }
  100% {
    width: 100px;
    height: 160px;
  }
}

@keyframes borderIncrease {
  100% {
    border-width: 20px 10px;
  }
}

@keyframes fadeIn {
  100% {
    color: #000;
    opacity: 1;
  }
}

@keyframes rotateRight {
  100% {
    transform: rotate(90deg);
  }
}

